import Vue from 'vue'
import VueRouter from 'vue-router'
// import { component } from 'vue/types/umd'
import TestComp from '../views/TestComp'
import TestThoughts from '../views/TestThoughts'
import TestPop from '../views/TestPop'
import RandomPicture from '../views/RandomPicture'
import RandomPicture1 from '../views/RandomPicture1'
Vue.use(VueRouter)

const routes = [
  {
    path: '/pic',
    name: 'RandomPicture',
    component: RandomPicture
  },
  {
    path: '/pic1',
    name: 'RandomPicture1',
    component: RandomPicture1
  },
  {
    path: '/music',
    name: 'TestComp',
    component: TestComp
  },
  {
    path: '/pop',
    name: 'TestPop',
    component: TestPop
  },
  {
    path: '/thoughts',
    name: 'TestThoughts',
    component: TestThoughts
  },
  {
    path: '/about',
    name: 'about',
    // route level code-splitting
    // this generates a separate chunk (about.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    component: () => import(/* webpackChunkName: "about" */ '../views/AboutView.vue')
  }
]

const router = new VueRouter({
  mode: 'hash',
  base: process.env.BASE_URL,
  routes
})

export default router
